#featured-slider-wrapper {
	overflow: hidden;

	#featured-slider {
		.slider-inner {
			.featured-slide {
				position: relative;
				min-height: 60vw;
				display: none;

				&.active {
					display: block;
				}

				.slider-title {
					position: relative;
					z-index: 100;
					font-family: $hfont;
					font-weight: bold;
					text-transform: uppercase;
					font-size: 120px;
					line-height: 0.8;
					letter-spacing: 0.04em;
					color: $yellow;
					max-width: 60%;

					.large {
						@include media($lg-mobile) {
							font-size: 280px;
						}
					}

					@include media($lg-mobile) {
						font-size: 200px;
					}

					@include media($md-tablet) {
						font-size: 230px;
					}

					@include media($md-desktop) {
						font-size: 16vw;
					}
				}

				.slider-image {
					display: block;
					@include size(40vw, 60vw);
					@include position(absolute, top -80px right 0);
					background-size: cover;
					background-position: top center;
				}

				.slider-link {
					position: relative;
					z-index: 100;
					padding: 18px 75px 18px 18px;
					background: $white;
					color: $darkgreen;
					margin-top: 50px;
					display: inline-block;
					font-weight: bold;
					text-transform: uppercase;
					line-height: 1;
					font-size: 18px;
					letter-spacing: 0.06em;

					@include media($lg-mobile) {
						margin-top: 100px;
					}

					.arrow {
						background: $blue;
						@include size(50px, 100%);
						@include position(absolute, top 0 right 0);

						&:before {
							content: "";
							display: block;
							background-image: url("#{$path-img}/arrow.svg");
							background-size: contain;
							background-position: center;
							background-repeat: no-repeat;
							@include size(15px, 26px);
							@include position(absolute, top 50% right 16px);
							transform: translateY(-50%);
						}
					}
				}

				.slider-indicators {
					display: flex;
					align-items: center;
					margin-top: 50px;
					cursor: pointer;

					.slider-indicator {
						@include size(12px, 12px);
						border-radius: 100%;
						background: $white;
						display: block;
						opacity: 0.1;
						margin-right: 10px;
						transition: all 0.25s ease-in-out;
						cursor: pointer;

						&.active {
							opacity: 1;
						}

						&:hover {
							opacity: 1;
						}
					}
				}
			}
		}
	}
}
